nav{
  display: flex;
  flex-wrap: nowrap;
  background-color: rgb(75, 74, 73);
  border-bottom: 7px rgb(15, 137, 137) solid;
  border-bottom-left-radius: 8%;
  border-bottom-right-radius: 8%;
  position: relative;
  left: 0;
  top: 0;
  right: 0;
  height: 9vh;
  align-items: center;
}

.logo{
  object-position: center;
  object-fit: fill;
  height: 50px;
  width: 50px;
  margin-left: 5px;
  margin-right: auto;
}

nav > ul{
  flex: 1;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

nav > ul > li{
  list-style: none;
  flex: 1;
  text-align: center;
  margin-right: 10px;
}

.nav_element{
  font-family: Lora, Roboto;
  font-size: 1.5em;
  color: rgb(19, 168, 168);
  font-weight: 900;
  text-decoration: none;
  cursor: pointer;
  transition: text-shadow 0.2s ease-in-out;
}

.nav_element:hover{
  text-shadow: 1px 0px 2px rgb(12, 244, 244);
}
